import * as React from 'react';
import {useEffect, useState} from 'react';
import {PropertiesPanel} from "./propertiesPanel/PropertiesPanel";
import {ProductOrderPanel} from "./productOrder/ProductOrderPanel";
import {engine} from "../model/MyEngine";
import {useForceRender} from "../useForceRender";
import {Paper} from "@material-ui/core";
import {InstructionsPanel} from "./instructionsPanel/InstructionsPanel";
import {DesignFlawsPanel} from "./DesignFlawsPanel";
import {HelpPanel} from "./HelpPanel";

export const Sidebar: React.FC = () => {
    const [showHelp, setShowHelp] = useState(false);
    const [designFlaws, setDesignFlaws] = useState(null);
    const [node, setNode] = useState(null);
    const forceUpdate = useForceRender();

    useEffect(() => {
        const listener = engine.registerListener({
            modelChanged: () => {
                setNode(null);
                setDesignFlaws(null);
                setShowHelp(false);
                forceUpdate();
            },
            showHelp: () => {
                setShowHelp(true);
            }
        }) ;
        return () => listener.deregister();
    });

    useEffect(() => {
        const listener = engine.getModel().registerListener({
            selectionChanged: (data: any) => {
                setNode(data.isSelected ? (data).node : null);
                setDesignFlaws(null);
                setShowHelp(false);
            },
            nodeRemoved: ({node: n}: any) => n === node && setNode(null),
            linkRemoved: ({link: l}: any) => l === node && setNode(null),
            designFlawsUpdated: ({flaws}: any) => {
                setShowHelp(false);
                setDesignFlaws(flaws)
            }
        });
        return () => listener.deregister();
    });


    return (
        <Paper style={{height: '100%', overflow: 'auto', borderRadius: 0}}>
            {showHelp ? (
                <HelpPanel/>
            ) : (designFlaws ? (
                <DesignFlawsPanel flaws={designFlaws || []}/>
            ) : (node ? (
                <PropertiesPanel node={node as any}/>
            ) : (
                engine.getModel().getNodes().length ? (
                    <ProductOrderPanel/>
                ) : (
                    <InstructionsPanel/>
                )
            )))}
        </Paper>
    )
};