import React from 'react'
import {Amazon} from "../data/productList";
import buyNowBtn from './buy-now-btn.png'

export const AmazonBuyNowBtn = ({amazon}: {amazon: Amazon}) => (
    <a href={amazon.textLink} target="_blank" rel="noopener noreferrer"><img src={buyNowBtn} height={16} alt="Buy now button"/></a>
);